html,body{width:100%;height:100%;margin:0;padding:0}

.navbar{width:100%;color:#fff;position:fixed;z-index:3;transition:all 1s ease;background-color:transparent}.navbar .wrapper{display:flex;align-items:center;justify-content:space-between;background-color:transparent}.navbar .wrapper Button{background-color:red}.navbar .wrapper .left{align-items:center}.navbar .wrapper .left .logo{font-size:50px;font-weight:700;text-decoration:none;color:inherit;font-family:"Gloria Hallelujah", cursive;padding-left:20px;align-items:center}.navbar .wrapper .left img{width:60px;height:60px;-webkit-filter:invert(1);filter:invert(1)}.navbar .wrapper .right .hamburger{width:32px;height:25px;display:flex !important;flex-direction:column;justify-content:space-between;cursor:pointer;padding-right:50px}.navbar .wrapper .right .hamburger span{width:100%;height:3px;background-color:#000000;-webkit-transform-origin:left;transform-origin:left;transition:all 2s ease}.navbar.active{background-color:rgba(0,0,0,0);color:white}.navbar.active .hamburger span:first-child{background-color:#fff;-webkit-transform:rotate(45deg);transform:rotate(45deg)}.navbar.active .hamburger span:nth-child(2){opacity:0}.navbar.active .hamburger span:last-child{background-color:#fff;-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}

.menu{width:300px;height:100vh;background-color:rgba(0,0,0,0.146);position:fixed;top:0;right:-300px;z-index:2;display:flex;flex-direction:column;align-items:center;justify-content:center;transition:all 1s ease}.menu.active{right:0}.menu ul{margin:0;padding:0;list-style:none;font-size:40px;font-weight:300;color:#fff;width:60%}.menu ul li{margin-bottom:25px}.menu ul li a{font-size:inherit;color:inherit;text-decoration:none}.menu ul li:hover{font-weight:700}

